import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Router } from '@angular/router';


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {

  email1: any = "";
  password1: any = "";
  emailT:any = "teacher@gmail.com";
  passwordT:any = "teacher1234";
  emailS:any="student@gmail.com"
  passwordS:any="student1234"


  getValue(data1, data2) {
    this.email1 = data1;
    this.password1 = data2;

    //teacher login logic
    if (this.email1 === this.emailT && this.password1 === this.passwordT) {
      this.router.navigate(['/teacher'])
    }
     // student login logic
    else if(this.email1===this.emailS && this.password1===this.passwordS){
      this.router.navigate(['/student'])
    }
    else {
      alert("wrong emial & password")
    }
}
  // login form validation
  login = new FormGroup({
    email: new FormControl('', Validators.required),
    password: new FormControl('', Validators.required),

  })

  //validation error message

  get vmail() {
    return this.login.get("email");
  }

  get vpwd() {
    return this.login.get("password")
  }



  constructor(private router:Router) { }

  ngOnInit() {
  }

}
