import { Component, OnInit } from '@angular/core';
import { StudentDataService } from '../services/student-data.service';
import { StudentSubMarksService } from '../services/student-sub-marks.service';

@Component({
  selector: 'app-teacher',
  templateUrl: './teacher.component.html',
  styleUrls: ['./teacher.component.scss']
})
export class TeacherComponent implements OnInit {


 subMark:any='';

  constructor(private http:StudentDataService,marks:StudentSubMarksService) { 
    this.subMark=marks.subjectMarks
  }
  // json data upload on table
  data:any=''
  ngOnInit() {
    this.http.getData().subscribe((res)=>{this.data=res})
  }  
// delete data from row 
  deleteData(index: number) {
    this.data.splice(index, 1);
  }
  // 1sts class toggle 
  show1=false


  // modal  toggle 
  myModal=false

  // image in profile
  image="https://images.unsplash.com/photo-1575936123452-b67c3203c357?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxzZWFyY2h8Mnx8aW1hZ2V8ZW58MHx8MHx8&w=1000&q=80"




  

}
